$top-panel-height: 35px;

$font-color: #233527;
$smallFont-size: 14px;

.app-panel {
    height: 100%;
    div {
        box-sizing: border-box; } }

.top-panel {
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    width: 100%;
    background: linear-gradient(to top, #f5feea, #ace6ba);
    height: $top-panel-height; }

.main-panel {
    position: fixed;
    display: flex;
    padding-top: $top-panel-height;
    width: 100%;
    height: 100vh; }
